
import { defineComponent } from 'vue';
import formatInput from "@/shared/components/FormatInput/FormatInput.vue";
import CurrencyInput from '@/shared/components/CurrencyInput/CurrencyInput.vue';
import { COM21000113_REQ_BODY, COM21000113_RES, ConfigurationInquiryLimit } from '@/shared/type/COM21000113.type';
import { COM21000132_REQ_BODY, COM21000132_RES } from '@/shared/type/COM2000132.type';
import { modalController } from '@ionic/vue';
import { APM20200113_RES } from "@/shared/type/APM20200113.type";
import { screenMode } from "@/shared/type/Approval.type";

export default defineComponent({
	name: "com210001ss02",
	components: {
		formatInput,
		CurrencyInput
	},

	data() {
		const limitBillPaymentTypeCode =  [
            { key: "01", value: "Limited" },
            { key: "02", value: "Unlimited" },
        ];
		const limitPayRollTypeCode =  [
            { key: "01", value: "Limited" },
            { key: "02", value: "Unlimited" },
        ];
		return {
			limitConfiguration: {
				bankMaxDailyLimitAmount: 0,
				bankMaxTransactionLimitAmount: 0,
				customerStandardDailyLimitAmount: 0,
				customerStandardTransactionLimitAmount: 0,
				transferToOtherAccountLimitAmount: 0,
				transferToOtherAccountDailyLimitAmount: 0,
				standingOrderLimitAmount: 0,
				overseaTransferLimitAmount: 0,
				openFixedDepositLimitAmount: 0,
				openFixedDepositDailyLimitAmount: 0,
				billPaymentLimitAmount: 0,
				billPaymentDailyLimitAmount: 0,
				payRollLimitAmount: 0,
				payRollDailyLimitAmount: 0,
				topUpLimitAmount: 0,
			},
			tempLimitConfiguration: [] as ConfigurationInquiryLimit[],
			exchangeRate: 4000,
			isDisable: true,
			approvalRes: {} as any,
			isDisabledAmountKHR:true,
			operationMode: 'normal' as screenMode,
			isExceedBankMaxTransaction: false,
			isExceedCustomerMaxTransaction: false,
			isExceedCustomerMaxDaily: false,
			isExceedBankMaxTransactionLimit: false,
			inValidBankMaxDailyAmount: false,
			inValidCustomerMaxDailyAmount: false,
			inValidCustomerMaxDailyLimit: false,
			inValidBankMaxTransactionAmount: false,
			inValidDailyTransferToOtherAccountAmount: false,
			isExceedTransferToOtherAccountAmount: false,
			inValidDailyOpenFixedDepositAmount: false,
			isExceedOpenFixedDepositAmount: false,
			isUnlimitedBillPaymentAmount:false,
			isUnlimitedBillPayRollAmount:false,
			isPayRollDailyLimitAmount:false,
			isPayRollTransactionLimitAmount:false,
			isExceedPayRollDailyLimitAmount:false,
			isExceedPayRollTransferLimitAmount:false,
			limitBillPaymentTypeCode,
			limitPayRollTypeCode,
			billPaymentOption: '01',
			payRollOption: '01'
		}
	},

	async mounted() {

		if (this.approvalType == '') {
			this.getConfigurationInquiryLimitList();
		} else {
			await this.$approval.getAuthorizationDetail().then((authDetail: APM20200113_RES) => {
				let authDetailParsed = JSON.parse(authDetail.body.requestContent);

				this.approvalRes = authDetail.body;
				this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal';

				this.$emit('operationMode', this.operationMode);
				this.$emit('approvalResponse', this.approvalRes);
				authDetailParsed.list.forEach((config: ConfigurationInquiryLimit) => {
					if (config.limitCategoryCode === '01') {
						switch (config.limitTypeCode) {
							case '00':
								if (config.transactionCurrencyCode === 'USD') {
									this.limitConfiguration.bankMaxDailyLimitAmount = config.maxDailyAmount
									this.limitConfiguration.bankMaxTransactionLimitAmount = config.maxTransactionAmount
									this.limitConfiguration.customerStandardDailyLimitAmount = config.defaultMaxDailyAmount
									this.limitConfiguration.customerStandardTransactionLimitAmount = config.defaultMaxTransactionAmount
								}
								break;
							case '02':
								// if (config.transactionCurrencyCode === 'USD') {
								// 	this.limitConfiguration.customerStandardDailyLimitAmount = config.maxDailyAmount
								// 	this.limitConfiguration.customerStandardTransactionLimitAmount = config.maxTransactionAmount
								// }
								break;
						}
					}
				})

			})
		}
	},

	watch:{
		billPaymentOption(newOption){
			if(newOption == '02'){
				this.isUnlimitedBillPaymentAmount = true;
				this.limitConfiguration.billPaymentLimitAmount = 0;
				this.limitConfiguration.billPaymentDailyLimitAmount = 0;
			}else{
				this.isUnlimitedBillPaymentAmount = false;
			}
		},

		payRollOption(newOption){
			if(newOption == '02'){
				this.isUnlimitedBillPayRollAmount = true;
				this.limitConfiguration.payRollDailyLimitAmount = 0;
				this.limitConfiguration.payRollLimitAmount = 0;
			}else{
				this.isUnlimitedBillPayRollAmount = false;
			}
		},

		'limitConfiguration.bankMaxDailyLimitAmount'(newValue){
			if(Number(newValue) >= this.limitConfiguration.bankMaxTransactionLimitAmount){
				this.isExceedBankMaxTransaction = false;
				this.inValidBankMaxDailyAmount = false;
				this.isExceedCustomerMaxDaily = false;
			}else if(Number(newValue) < this.limitConfiguration.bankMaxTransactionLimitAmount){
				this.inValidBankMaxDailyAmount = true;
			}
		},
		'limitConfiguration.bankMaxTransactionLimitAmount'(newValue){
			if(Number(newValue) > this.limitConfiguration.bankMaxDailyLimitAmount){
				this.isExceedBankMaxTransaction = true;
				this.inValidBankMaxTransactionAmount = false;
			}else if(Number(newValue) < this.limitConfiguration.customerStandardTransactionLimitAmount){
				this.inValidBankMaxTransactionAmount = true
				this.isExceedBankMaxTransaction = false;
			}else if(Number(newValue) > this.limitConfiguration.customerStandardTransactionLimitAmount){
				this.inValidBankMaxTransactionAmount = false;
				this.isExceedBankMaxTransaction = false;
				this.inValidBankMaxDailyAmount = false;
			}else if(Number(newValue) < this.limitConfiguration.bankMaxDailyLimitAmount){
				this.inValidBankMaxDailyAmount = false;
			}
		},

		'limitConfiguration.customerStandardTransactionLimitAmount'(newValue){
			if(Number(newValue) > this.limitConfiguration.customerStandardDailyLimitAmount && Number(newValue) < this.limitConfiguration.bankMaxTransactionLimitAmount){
				this.isExceedCustomerMaxTransaction = true;
				this.isExceedBankMaxTransactionLimit = false;
			}else if(Number(newValue) > this.limitConfiguration.customerStandardDailyLimitAmount && Number(newValue) > this.limitConfiguration.bankMaxTransactionLimitAmount){
				this.isExceedBankMaxTransactionLimit = true;
				this.isExceedCustomerMaxTransaction = false;
			}else if(Number(newValue) < this.limitConfiguration.customerStandardDailyLimitAmount && Number(newValue) > this.limitConfiguration.bankMaxTransactionLimitAmount){
				this.isExceedBankMaxTransactionLimit = true;
				this.isExceedCustomerMaxTransaction = false;
			}
			else if (Number(newValue) < this.limitConfiguration.bankMaxTransactionLimitAmount){
				this.inValidCustomerMaxDailyAmount = false;
				this.isExceedCustomerMaxTransaction = false;
				this.inValidBankMaxTransactionAmount = false;
				this.isExceedBankMaxTransactionLimit = false;
				this.inValidCustomerMaxDailyLimit = false;
			}else if (Number(newValue) < this.limitConfiguration.customerStandardDailyLimitAmount){
				this.inValidCustomerMaxDailyAmount = false;
				this.isExceedCustomerMaxTransaction = false;
				this.inValidCustomerMaxDailyLimit = false;
			}
		},
		'limitConfiguration.customerStandardDailyLimitAmount'(newValue){
			if(Number(newValue) > this.limitConfiguration.bankMaxDailyLimitAmount){
				this.isExceedCustomerMaxDaily = true;
				this.inValidCustomerMaxDailyLimit = false;
			}else if(Number(newValue) < this.limitConfiguration.bankMaxDailyLimitAmount){
				this.isExceedCustomerMaxDaily = false;
				if(Number(newValue) < this.limitConfiguration.customerStandardTransactionLimitAmount){
					this.inValidCustomerMaxDailyLimit = true;
					this.isExceedCustomerMaxTransaction = false;
				}else{
					this.isExceedCustomerMaxTransaction = false;
					this.inValidCustomerMaxDailyLimit = false;
				}
			}
			else if(Number(newValue) > this.limitConfiguration.customerStandardTransactionLimitAmount){
				this.isExceedCustomerMaxTransaction = false;
				this.isExceedCustomerMaxDaily = false;
				this.inValidCustomerMaxDailyAmount = false;
				this.inValidCustomerMaxDailyLimit = false;
			}else if(Number(newValue) < this.limitConfiguration.customerStandardTransactionLimitAmount){
				this.inValidCustomerMaxDailyAmount = true;
			}
		},

		'limitConfiguration.transferToOtherAccountDailyLimitAmount'(newTransferValue){
			if(Number(newTransferValue) < this.limitConfiguration.transferToOtherAccountLimitAmount){
				this.inValidDailyTransferToOtherAccountAmount = true;
			}else{
				this.inValidDailyTransferToOtherAccountAmount = false;
				this.isExceedTransferToOtherAccountAmount = false;
			}
		},

		'limitConfiguration.transferToOtherAccountLimitAmount'(newTransferValue){
			if(Number(newTransferValue) > this.limitConfiguration.transferToOtherAccountDailyLimitAmount){
				this.isExceedTransferToOtherAccountAmount = true;
			}else{
				this.isExceedTransferToOtherAccountAmount = false;
				this.inValidDailyTransferToOtherAccountAmount = false;
			}
		},

		'limitConfiguration.openFixedDepositDailyLimitAmount'(newDailyOpenFixedDeposit){
			if(Number(newDailyOpenFixedDeposit) < this.limitConfiguration.openFixedDepositLimitAmount){
				this.inValidDailyOpenFixedDepositAmount = true;
			}else{
				this.inValidDailyOpenFixedDepositAmount = false;
				this.isExceedOpenFixedDepositAmount = false;
			}
		},

		'limitConfiguration.openFixedDepositLimitAmount'(newOpenFixedDeposit){
			if(Number(newOpenFixedDeposit) > this.limitConfiguration.openFixedDepositDailyLimitAmount){
				this.isExceedOpenFixedDepositAmount = true;
			}else{
				this.isExceedOpenFixedDepositAmount = false;
				this.inValidDailyOpenFixedDepositAmount = false;
			}
		},

		'limitConfiguration.billPaymentDailyLimitAmount'(newValue){
			if(newValue < this.limitConfiguration.billPaymentLimitAmount){
				this.isPayRollDailyLimitAmount = true;
			}else{
				this.isPayRollDailyLimitAmount = false;
				this.isPayRollTransactionLimitAmount = false;
			}
		},

		'limitConfiguration.billPaymentLimitAmount'(newValue){
			if(newValue > this.limitConfiguration.billPaymentDailyLimitAmount){
				this.isPayRollTransactionLimitAmount = true;
			}else{
				this.isPayRollTransactionLimitAmount = false;
				this.isPayRollDailyLimitAmount = false;	
			}
		},

		'limitConfiguration.payRollDailyLimitAmount'(newValue){
			if(newValue < this.limitConfiguration.payRollLimitAmount){
				this.isExceedPayRollDailyLimitAmount = true;
			}else{
				this.isExceedPayRollDailyLimitAmount = false;
				this.isExceedPayRollTransferLimitAmount = false;
			}
		},

		'limitConfiguration.payRollLimitAmount'(newValue){
			if(newValue > this.limitConfiguration.payRollDailyLimitAmount){
				this.isExceedPayRollTransferLimitAmount = true;
			}else{
				this.isExceedPayRollTransferLimitAmount = false;
				this.isExceedPayRollDailyLimitAmount = false;
			}
		}
	},

	computed: {
		approvalType(): string {
			return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : '';
		},
		bankMaxDailyLimitAmountKHR(): number {
			return this.limitConfiguration.bankMaxDailyLimitAmount * this.exchangeRate;
		},
		bankMaxTransactionLimitAmountKHR(): number {
			return this.limitConfiguration.bankMaxTransactionLimitAmount * this.exchangeRate;
		},
		customerStandardDailyLimitAmountKHR(): number {
			return this.limitConfiguration.customerStandardDailyLimitAmount * this.exchangeRate;
		},
		customerStandardTransactionLimitAmountKHR(): number {
			return this.limitConfiguration.customerStandardTransactionLimitAmount * this.exchangeRate;
		},
		transferToOtherAccountLimitAmountKHR(): number {
			return this.limitConfiguration.transferToOtherAccountLimitAmount * this.exchangeRate;
		},
		transferToOtherAccountDailyLimitAmountKHR(): number {
			return this.limitConfiguration.transferToOtherAccountDailyLimitAmount * this.exchangeRate;
		},
		standingOrderLimitAmountKHR(): number {
			return this.limitConfiguration.standingOrderLimitAmount * this.exchangeRate;
		},
		overseaTransferLimitAmountKHR(): number {
			return this.limitConfiguration.overseaTransferLimitAmount * this.exchangeRate;
		},
		openFixedDepositLimitAmountKHR(): number {
			return this.limitConfiguration.openFixedDepositLimitAmount * this.exchangeRate;
		},
		openFixedDepositDailyLimitAmountKHR(): number {
			return this.limitConfiguration.openFixedDepositDailyLimitAmount * this.exchangeRate;
		},
		billPaymentLimitAmountKHR(): number {
			return this.limitConfiguration.billPaymentLimitAmount * this.exchangeRate;
		},
		billPaymentDailyLimitAmountKHR(): number {
			return this.limitConfiguration.billPaymentDailyLimitAmount * this.exchangeRate;
		},
		payRollLimitAmountKHR(): number {
			return this.limitConfiguration.payRollLimitAmount * this.exchangeRate;
		},
		payRollDailyLimitAmountKHR(): number {
			return this.limitConfiguration.payRollLimitAmount * this.exchangeRate;
		},
		topUpLimitAmountKHR(): number {
			return this.limitConfiguration.topUpLimitAmount * this.exchangeRate;
		},
	},

	methods: {
		handleEvent(event: string) {
			switch (event) {
				case 'cancel':
					this.$dialog.onOpen({
						dialogMode: "Question",
						content: `${this.$t("message.are_you_sure_you_want_to_cancel_without_saving_data")}`,
						callback: () => {
							this.isDisable = true;
							this.getConfigurationInquiryLimitList();
							this.$emit('isResetEditable', false);
							this.$emit('isTapChange', true);
							modalController.dismiss();
						}
					});
					break;

				case 'edit':
					this.isDisable = false;
					break;

				case 'save':
					if(!this.isExceedCustomerMaxTransaction && !this.isExceedBankMaxTransaction && 
						!this.isExceedCustomerMaxDaily && !this.isExceedBankMaxTransactionLimit
						&& !this.inValidBankMaxDailyAmount && !this.inValidCustomerMaxDailyAmount 
						&& !this.inValidCustomerMaxDailyLimit && !this.inValidBankMaxTransactionAmount
						&& !this.inValidDailyTransferToOtherAccountAmount && !this.isExceedTransferToOtherAccountAmount
						&& !this.inValidDailyOpenFixedDepositAmount && !this.isExceedOpenFixedDepositAmount
					){
						this.$dialog.onOpen({
							dialogMode: "Question",
							content: `${this.$t("message.would_you_like_to_save")}`,
							callback: () => {
								this.isDisable = true;
								this.updateConfigurationInquiryLimitList();
								this.$emit('isResetEditable', false);
								this.$emit('isTapChange', true);
								modalController.dismiss();
							}
						});
					}
					break;

				default:
					break;
			}
		},

		async getConfigurationInquiryLimitList() {
			const reqBody: COM21000113_REQ_BODY = {
				limitCategoryCode: "01",
				limitTypeCode: "00,01,02,03,04,05,06,07"
			}

			const resData = await this.$request.getRequest("COM21000113", reqBody, false) as COM21000113_RES
			this.tempLimitConfiguration = resData.body.list;

			resData.body.list.forEach((config: ConfigurationInquiryLimit) => {
				if (config.limitCategoryCode === '01') {
					switch (config.limitTypeCode) {
						case '00':
							if (config.transactionCurrencyCode === 'USD') {
								this.limitConfiguration.bankMaxDailyLimitAmount = config.maxDailyAmount
								this.limitConfiguration.bankMaxTransactionLimitAmount = config.maxTransactionAmount
								this.limitConfiguration.customerStandardDailyLimitAmount = config.defaultMaxDailyAmount
								this.limitConfiguration.customerStandardTransactionLimitAmount = config.defaultMaxTransactionAmount
							}
							break;
						case '01':
							if (config.transactionCurrencyCode === 'USD') {
								this.limitConfiguration.transferToOtherAccountLimitAmount = config.maxTransactionAmount;
								this.limitConfiguration.transferToOtherAccountDailyLimitAmount = config.maxDailyAmount;
							}
							break;
						case '06':
							if(config.transactionCurrencyCode === 'USD') {
								this.limitConfiguration.standingOrderLimitAmount = config.maxTransactionAmount;
							}
							break;
						
						case '04':
							if(config.transactionCurrencyCode === 'USD') {
								this.limitConfiguration.overseaTransferLimitAmount = config.maxTransactionAmount;
							}
							break;

						case '03':
							if(config.transactionCurrencyCode === 'USD') {
								this.limitConfiguration.openFixedDepositLimitAmount = config.maxTransactionAmount;
								this.limitConfiguration.openFixedDepositDailyLimitAmount = config.maxDailyAmount;
							}
							break;

						case '05':
							if(config.transactionCurrencyCode === 'USD') {
								this.limitConfiguration.billPaymentLimitAmount = config.maxTransactionAmount;
								this.limitConfiguration.billPaymentDailyLimitAmount = config.maxDailyAmount;
							}
							break;

						case '02':
							if(config.transactionCurrencyCode === 'USD') {
								this.limitConfiguration.payRollDailyLimitAmount = config.maxDailyAmount;
								this.limitConfiguration.payRollLimitAmount = config.maxTransactionAmount;
							}
							break;

						case '07':
							if(config.transactionCurrencyCode === 'USD') {
								this.limitConfiguration.topUpLimitAmount = config.maxTransactionAmount;
							}
							break;
					}
				}
			})
		},

		handleMaxTransactionAmount(limitTypeCode:string): number {
			let maxTransactionAmount = 0
				if(limitTypeCode == '00'){
					maxTransactionAmount =  Number(this.limitConfiguration.bankMaxTransactionLimitAmount);
				}
				else if(limitTypeCode == '01'){
					maxTransactionAmount  = Number(this.limitConfiguration.transferToOtherAccountLimitAmount);
				}
				else if(limitTypeCode == '06'){
					maxTransactionAmount = Number(this.limitConfiguration.standingOrderLimitAmount);
				}
				else if(limitTypeCode == '04'){
					maxTransactionAmount = Number(this.limitConfiguration.overseaTransferLimitAmount);
				}
				else if(limitTypeCode == '03'){
					maxTransactionAmount = Number(this.limitConfiguration.openFixedDepositLimitAmount);
				}
				else if(limitTypeCode == '05'){
					maxTransactionAmount = Number(this.limitConfiguration.billPaymentLimitAmount);
				}
				else if(limitTypeCode == '02'){
					maxTransactionAmount = Number(this.limitConfiguration.payRollLimitAmount);
				}
				else if(limitTypeCode == '07'){
					maxTransactionAmount = Number(this.limitConfiguration.topUpLimitAmount);
				}
			return maxTransactionAmount
		},

		handleMaxDailyAmount(limitTypeCode: string){
			let maxDailyAmount = 0
			if(limitTypeCode == '00'){
				maxDailyAmount = Number(this.limitConfiguration.bankMaxDailyLimitAmount);
			}else if(limitTypeCode == '01'){
				maxDailyAmount = Number(this.limitConfiguration.transferToOtherAccountDailyLimitAmount);
			}else if(limitTypeCode == '03'){
				maxDailyAmount = Number(this.limitConfiguration.openFixedDepositDailyLimitAmount);
			}else if(limitTypeCode == '02'){
				maxDailyAmount = Number(this.limitConfiguration.payRollDailyLimitAmount);
			}else if(limitTypeCode == '05'){
				maxDailyAmount = Number(this.limitConfiguration.billPaymentDailyLimitAmount);
			}
			return maxDailyAmount;
		},

		async updateConfigurationInquiryLimitList() {
			const reqBody: COM21000132_REQ_BODY = {
				list: this.tempLimitConfiguration.map((config: ConfigurationInquiryLimit) => {
					return {
						limitCategoryCode: config.limitCategoryCode,
						limitTypeCode: config.limitTypeCode,
						maxDailyAmount: this.handleMaxDailyAmount(config.limitTypeCode),
						minTransactionAmount: config.minTransactionAmount,
						maxTransactionAmount: this.handleMaxTransactionAmount(config.limitTypeCode),
						transactionCurrencyCode: config.transactionCurrencyCode,
						defaultMaxDailyAmount: config.limitTypeCode == '00' ? Number(this.limitConfiguration.customerStandardDailyLimitAmount) : config.defaultMaxDailyAmount,
						defaultMaxTransactionAmount: config.limitTypeCode == '00' ? Number(this.limitConfiguration.customerStandardTransactionLimitAmount) : config.defaultMaxTransactionAmount,
						limitDescription: config.limitDescription,
					}
				})
			};

			const responseData = await this.$request.getRequest("COM21000132", reqBody, false, 'COM210001SS02') as COM21000132_RES;
			if (this.approvalType) {
				this.isDisable = true;
			}

			if (responseData.header.result) {
				this.getConfigurationInquiryLimitList();
				this.isDisable = true;
				this.$dialog.onOpen({
					content: `${this.$t("message.update_success")}`,
					dialogMode: "Success",
					callback: () => {
						modalController.dismiss();
					}
				});
			} else {
				console.log(responseData.header.error_text);
			}
		},
	}
})
